let cost1 = document.querySelector('.slide5_cost1');
let cost2 = document.querySelector('.slide5_cost2');
let cost3 = document.querySelector('.slide5_cost3');


function toChangeTranslate(translate) {
    document.querySelector('.slide5_item__fh').style.transform = `translateX(${translate}%)`;
}


function toMoveSlide(current, target) {
    if (current < target) {
        console.log(current, target);
        let timer = setInterval(function() {
            current += 0.3;
            toChangeTranslate(current);
            console.log(current, target);
            if (current >= target) {
                clearInterval(timer);
            };
        },1);
    }
    else if (current > target) {
        console.log(current, target);
        let timer = setInterval(function() {
            current -= 0.3;
            toChangeTranslate(current);
            console.log(current, target);
            if (current <= target) {
                clearInterval(timer);
            };
        },1);
    };
}

function toChangeToCost1() {
    console.log('toChangeToCost1!');
    let currentTranslate = parseFloat((document.querySelector('.slide5_item__fh').style.transform).slice(11, 16));
    document.querySelector('.slide5_cost1').style.fontWeight='bold';
    document.querySelector('.slide5_cost2').style.fontWeight='';
    document.querySelector('.slide5_cost3').style.fontWeight='';
    toMoveSlide(currentTranslate, 33,3);
}

function toChangeToCost2() {
    console.log('toChangeToCost2!');
    let currentTranslate = parseFloat((document.querySelector('.slide5_item__fh').style.transform).slice(11, 16));
    document.querySelector('.slide5_cost1').style.fontWeight='';
    document.querySelector('.slide5_cost2').style.fontWeight='bold';
    document.querySelector('.slide5_cost3').style.fontWeight='';
    toMoveSlide(currentTranslate, 0);
}

function toChangeToCost3() {
    console.log('toChangeToCost3!');
    let currentTranslate = parseFloat((document.querySelector('.slide5_item__fh').style.transform).slice(11, 16));
    document.querySelector('.slide5_cost1').style.fontWeight='';
    document.querySelector('.slide5_cost2').style.fontWeight='';
    document.querySelector('.slide5_cost3').style.fontWeight='bold';
    toMoveSlide(currentTranslate, -33,3);
}

cost1.addEventListener("click", function() {
    console.log('click');
    toChangeToCost1();
}, false);

cost2.addEventListener("click", function() {
    console.log('click');
    toChangeToCost2();
}, false);

cost3.addEventListener("click", function() {
    console.log('click');
    toChangeToCost3();
}, false);